// API Client for Vizora Middleware

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3000/api';

class ApiClient {
  private baseUrl: string;
  private token: string | null = null;

  constructor(baseUrl: string) {
    this.baseUrl = baseUrl;
    // Load token from localStorage if available
    if (typeof window !== 'undefined') {
      this.token = localStorage.getItem('authToken');
    }
  }

  setToken(token: string) {
    this.token = token;
    if (typeof window !== 'undefined') {
      localStorage.setItem('authToken', token);
    }
  }

  clearToken() {
    this.token = null;
    if (typeof window !== 'undefined') {
      localStorage.removeItem('authToken');
    }
  }

  private async request<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<T> {
    const headers: HeadersInit = {
      'Content-Type': 'application/json',
      ...(this.token && { Authorization: `Bearer ${this.token}` }),
      ...options.headers,
    };

    const response = await fetch(`${this.baseUrl}${endpoint}`, {
      ...options,
      headers,
    });

    if (!response.ok) {
      const error = await response.json().catch(() => ({ message: 'Request failed' }));
      throw new Error(error.message || `HTTP ${response.status}`);
    }

    return response.json();
  }

  // Auth
  async login(email: string, password: string) {
    const data = await this.request<{ access_token: string }>('/auth/login', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
    this.setToken(data.access_token);
    return data;
  }

  async register(email: string, password: string, organizationName: string) {
    return this.request('/auth/register', {
      method: 'POST',
      body: JSON.stringify({ email, password, organizationName }),
    });
  }

  // Displays
  async getDisplays(params?: { page?: number; limit?: number }) {
    const query = new URLSearchParams(params as any).toString();
    return this.request<any>(`/displays${query ? `?${query}` : ''}`);
  }

  async getDisplay(id: string) {
    return this.request<any>(`/displays/${id}`);
  }

  async createDisplay(data: { nickname: string; location?: string }) {
    return this.request<any>('/displays', {
      method: 'POST',
      body: JSON.stringify(data),
    });
  }

  async updateDisplay(id: string, data: Partial<{ nickname: string; location?: string }>) {
    return this.request<any>(`/displays/${id}`, {
      method: 'PATCH',
      body: JSON.stringify(data),
    });
  }

  async deleteDisplay(id: string) {
    return this.request<any>(`/displays/${id}`, {
      method: 'DELETE',
    });
  }

  async generatePairingToken(id: string) {
    return this.request<{ pairingCode: string }>(`/displays/${id}/pair`, {
      method: 'POST',
    });
  }

  // Content
  async getContent(params?: { page?: number; limit?: number; type?: string; status?: string }) {
    const query = new URLSearchParams(params as any).toString();
    return this.request<any>(`/content${query ? `?${query}` : ''}`);
  }

  async getContentItem(id: string) {
    return this.request<any>(`/content/${id}`);
  }

  async createContent(data: {
    title: string;
    type: string;
    url?: string;
    metadata?: any;
  }) {
    return this.request<any>('/content', {
      method: 'POST',
      body: JSON.stringify(data),
    });
  }

  async updateContent(id: string, data: Partial<{ title: string; metadata?: any }>) {
    return this.request<any>(`/content/${id}`, {
      method: 'PATCH',
      body: JSON.stringify(data),
    });
  }

  async deleteContent(id: string) {
    return this.request<any>(`/content/${id}`, {
      method: 'DELETE',
    });
  }

  async archiveContent(id: string) {
    return this.request<any>(`/content/${id}/archive`, {
      method: 'POST',
    });
  }

  // Playlists
  async getPlaylists(params?: { page?: number; limit?: number }) {
    const query = new URLSearchParams(params as any).toString();
    return this.request<any>(`/playlists${query ? `?${query}` : ''}`);
  }

  async getPlaylist(id: string) {
    return this.request<any>(`/playlists/${id}`);
  }

  async createPlaylist(data: {
    name: string;
    description?: string;
    items?: Array<{ contentId: string; duration?: number }>;
  }) {
    return this.request<any>('/playlists', {
      method: 'POST',
      body: JSON.stringify(data),
    });
  }

  async updatePlaylist(id: string, data: Partial<{
    name: string;
    description?: string;
  }>) {
    return this.request<any>(`/playlists/${id}`, {
      method: 'PATCH',
      body: JSON.stringify(data),
    });
  }

  async deletePlaylist(id: string) {
    return this.request<any>(`/playlists/${id}`, {
      method: 'DELETE',
    });
  }

  async addPlaylistItem(playlistId: string, contentId: string, duration?: number) {
    return this.request<any>(`/playlists/${playlistId}/items`, {
      method: 'POST',
      body: JSON.stringify({ contentId, duration }),
    });
  }

  async removePlaylistItem(playlistId: string, itemId: string) {
    return this.request<any>(`/playlists/${playlistId}/items/${itemId}`, {
      method: 'DELETE',
    });
  }

  // Schedules
  async getSchedules(params?: { page?: number; limit?: number }) {
    const query = new URLSearchParams(params as any).toString();
    return this.request<any>(`/schedules${query ? `?${query}` : ''}`);
  }

  async createSchedule(data: any) {
    return this.request<any>('/schedules', {
      method: 'POST',
      body: JSON.stringify(data),
    });
  }

  async updateSchedule(id: string, data: any) {
    return this.request<any>(`/schedules/${id}`, {
      method: 'PATCH',
      body: JSON.stringify(data),
    });
  }

  async deleteSchedule(id: string) {
    return this.request<any>(`/schedules/${id}`, {
      method: 'DELETE',
    });
  }
}

export const apiClient = new ApiClient(API_BASE_URL);
